import Vue from 'vue'
import vm from '../../globalVM'
import error from '../../components/Error/Error'
import loading from '../../components/Loading/Loading'
import flexible from '../../utils/simple-flexible'
import navBar from '../../components/NavBar/NavBar'
import imageLoad from '../../components/ImageLoad/ImageLoad'
import searchBoard from '../../components/SearchBoard/SearchBoard'
import mainGoodsList from '../../components/MainGoodsList/MainGoodsList'
import  '../../resoures/css/animate.css'

import  '../../resoures/css/main.scss'
import  '../../resoures/css/common.scss'
import  '../../resoures/font/iconfont.css'

import './List.scss'
import api from '../../models/api'
import * as utils from '../../utils/utils'

//import fastclick from 'fastclick';
import 'babel-polyfill';
//import 'lodash'
flexible()


// FOR H5 处理移动端click 延时 300ms的问题
// fastclick.attach(document.body);

// 根据当前环境选择性加载 mock 包，发布的时候也不用注释和删除
if(process.env.NODE_ENV === 'development') {
  require('./mock');
}



new Vue({
  el: '#app',
  components: {
  	navBar,
  	searchBoard,
  	imageLoad,
  	error,
  	loading,
    mainGoodsList
  },
  data() {
  	return {
      searchBoardVisible: false,
      scrollTop: "",
  		isError: false,
      title: "",
      listTypeId: ""
  	}
  },
  computed: {
    isLoading() {
      return vm.isLoading
    }
  },
  created() {
    vm.$on("reload", this.reset)
    if(utils.getQueryString("ip")) {
      this.listType = "ip"
      this.listTypeId = utils.getQueryString("ip")
    } else if(utils.getQueryString("brand")) {
      this.listType = "brand"
      this.listTypeId = utils.getQueryString("brand")
    } else if(utils.getQueryString("category")) {
      this.listType = "category"
      this.listTypeId = utils.getQueryString("category")
    } else if(utils.getQueryString("attr")) {
      this.listType = "attr"
      this.listTypeId = utils.getQueryString("attr")      
    } else{
      this.listType = "keyword"
    }

  },
  mounted() {
    window.addEventListener("scroll", this.scroll)
  },
  methods: {
    back() {
      utils.back()
    },
    onSearch() {
      this.searchBoardVisible = true;
      this.$nextTick(()=>{
        this.$refs.searchBoard.$refs.searchInput.focus();
      })
    },
    reset() {
      this.$nextTick(()=>{
        this.$refs.head.style.background = "";
        this.$refs.head.style.top = "0px";
        this.$refs.head.style.display = "block";
        this.$refs.mainHead.style.opacity = 1;

      })
    },
    getData(t) {
      vm.isLoading = false;
      if(t) {
        this.hasResult = true;
        if("Figure" === t.pageTitle) {
           this.title = "Figure"
         } else {
            this.title = t.pageTitle
         }
      } else {
        this.hasResult = false
      }
    },
  	scroll() {
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
      const head = this.$refs.head;
      const mainHead = this.$refs.mainHead;
      const distance= this.scrollTop - scrollTop;  //根据上次滚动的位置判断: >=0判断向上滚动，<0p判断为向下滚动
      if(scrollTop > 44) {
        if(distance >= 0){
          head.style.top = "0";
          head.style.background = "";
          mainHead.style.opacity = 1
          head.style.display = "block"
          
        } else {
          head.style.top = "-44px";
          head.style.background = "#FFF";
          head.style.display = "none"
          mainHead.style.opacity = 0
        }
      } else {
        "-44px" === head.style.top && (head.style.top = "0px", head.style.background = "",head.style.display = "block", mainHead.style.opacity = 1)
      }
      this.searchBoardVisible || (this.scrollTop = scrollTop) //每次滚动时记录当前的位置
    }
  }
})


